import inputCell from '@/components/input-cell'
import payMethod from '@/components/pay-method'
import { mapState, mapActions } from 'vuex'
import { recharge } from '@/api'
export default {
	name: '',
	data () {
		return {
			type: 1, // 1微信  2  支付宝
			money: ''
		}
	},
	onLoad () {

	},
	methods: {
		...mapActions(['reqGetUserInfo']),
		_reqGetUserInfo () {
			this.reqGetUserInfo ({
				token: uni.getStorageSync('token')
			})
		},
		async onConfirmClick () {
			let self = this;
			if(this.money == '') {
				uni.showToast({
					title: '请输入充值金额',
					icon: 'none'
				})
				return;
			}
			let result = await recharge ({
				token: uni.getStorageSync('token'),
				dsf: this.type,
				price: this.money
			})
			let provider;
			if(this.type == 1) {
				provider = 'wxpay'
			}else {
				provider = 'alipay'
			}
			console.log(result, provider)
			uni.requestPayment({
				provider: provider,
				orderInfo: result, //微信、支付宝订单数据
				success: function (res) {
					self._reqGetUserInfo()
					uni.redirectTo({
						url: '/pages/mine/subPages/success/success'
					})
				},
				fail: function (err) {
					console.log('fail:' + JSON.stringify(err));
				}
			});
			return;
			uni.showModal({
				title: '充值',
				content: '充值成功',
				showCancel: false,
				success: function (res) {
					if(res.confirm) {
						
					}
				}
			})
		},
		onPayMethodClick (type) {
			this.type = type;
		}
	},
	components: {
		inputCell,
		payMethod
	}
}
